.container {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
}
.hero {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 2;
  background-color: #f4f4f4;
}
.hero h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}
.hero p {
  font-size: 1.5rem;
  color: #666;
}
.cardsSection {
  padding: 0 20px;
  max-width: 800px;
  margin: 0 auto;
  padding-bottom: 20vh;
  position: relative;
  z-index: 2;
  background-color: #f4f4f4;
}
.cardContainer {
  position: sticky;
  height: 400px;
  margin-bottom: 40px;
  display: flex;
  justify-content: center;
}
.card {
  width: 100%;
  height: 100%;
  border-radius: 24px;
  padding: 40px;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.1);
  transform-origin: center top;
}
.card h2 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: white;
}
.card p {
  font-size: 1.2rem;
  opacity: 0.9;
}
.horizontalScrollSection {
  height: 300vh;
  position: relative;
  z-index: 2;
  background-color: #1a1a1a;
}
.horizontalStickyWrapper {
  position: sticky;
  top: 0;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: #1a1a1a;
  color: #fff;
}
.horizontalHeader {
  position: absolute;
  top: 10%;
  left: 5vw;
  z-index: 10;
}
.horizontalHeader h2 {
  font-size: 3rem;
  color: #fff;
  margin-bottom: 1rem;
}
.horizontalHeader p {
  font-size: 1.2rem;
  color: #aaa;
}
.horizontalTrack {
  display: flex;
  gap: 60px;
  padding-left: 5vw;
}
.horizontalCard {
  flex: 0 0 400px;
  height: 500px;
  background: #2d2d2d;
  border-radius: 24px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 20px 50px rgba(0, 0, 0, 0.3);
  border: 1px solid #444;
}
.horizontalCard h3 {
  color: #fff;
  font-size: 1.8rem;
  margin-bottom: 1rem;
}
.horizontalCard .mockUI {
  width: 100%;
  height: 200px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  margin-top: 20px;
}
.stickyFeatureSection {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 100px 20px;
  position: relative;
  z-index: 2;
  background: #fff;
}
.featureContent {
  width: 50%;
  padding-right: 60px;
}
.featureTextBlock {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0.2;
  transition: opacity 0.5s ease;
}
.featureTextBlock h2 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
  color: #000;
}
.featureTextBlock p {
  font-size: 1.2rem;
  line-height: 1.6;
  color: #555;
}
.featureTextBlock.active {
  opacity: 1;
}
.featureImages {
  width: 50%;
  height: 100vh;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imageWrapper {
  position: absolute;
  width: 100%;
  height: 600px;
  border-radius: 24px;
  overflow: hidden;
  box-shadow: 0 30px 60px rgba(0, 0, 0, 0.15);
  background: #f0f0f0;
}
.imageWrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.framerMarqueeSection {
  padding: 100px 0;
  background: #050505;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  gap: 32px;
  position: relative;
  z-index: 2;
}
.framerMarqueeSection h2 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 40px;
  color: #fff;
}
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 80vh;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.marqueeSection {
  padding: 100px 0;
  background: #fff;
  overflow: hidden;
  position: relative;
  z-index: 2;
}
.marqueeSection h2 {
  text-align: center;
  font-size: 3rem;
  margin-bottom: 60px;
  color: #000;
}
.resourceCard {
  width: 300px;
  height: 200px;
  background: #f5f5f5;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.05);
  transition: transform 0.3s ease;
  flex-shrink: 0;
}
.resourceCard:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
}
.resourceCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.resourceCard .resourceInfo {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 16px;
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8), transparent);
  color: #fff;
}
.resourceCard .resourceInfo h4 {
  margin: 0;
  font-size: 1.1rem;
  color: #fff;
}
.resourceCard .resourceInfo span {
  font-size: 0.9rem;
  opacity: 0.8;
}
.marqueeRow {
  display: flex;
  width: 100%;
  overflow: visible;
}
.marqueeTrack {
  display: flex;
  gap: 32px;
  width: max-content;
  padding: 10px 0;
}
.marqueeCard {
  width: 280px;
  height: 360px;
  border-radius: 16px;
  overflow: hidden;
  position: relative;
  flex-shrink: 0;
  background: #1a1a1a;
  transition: transform 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  cursor: pointer;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.2);
}
.marqueeCard:hover {
  transform: scale(1.05);
  z-index: 10;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.4);
}
.marqueeCard img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.5s ease;
}
.marqueeCard:hover img {
  transform: scale(1.1);
}
.marqueeCard .cardContent {
  position: absolute;
  inset: 0;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
}
.marqueeCard .cardContent h3 {
  font-size: 1.8rem;
  font-weight: 700;
  line-height: 1.1;
  margin-bottom: 12px;
}
.marqueeCard .cardContent p {
  font-size: 1rem;
  opacity: 0.8;
  line-height: 1.4;
}
.marqueeCard .cardContent .btn {
  align-self: flex-start;
  padding: 8px 20px;
  border-radius: 20px;
  background: #fff;
  color: #000;
  font-weight: 600;
  font-size: 0.9rem;
  margin-top: auto;
}
.marqueeCard.card-text-white {
  background: #fff;
  color: #000;
}
.marqueeCard.card-text-white .cardContent .btn {
  background: #000;
  color: #fff;
}
.marqueeCard.card-text-dark {
  background: #111;
  color: #fff;
}
.marqueeCard.card-gradient-1 {
  background: linear-gradient(135deg, #ff4d4d, #f9cb28);
  color: #fff;
}
.marqueeCard.card-gradient-2 {
  background: linear-gradient(135deg, #7928ca, #ff0080);
  color: #fff;
}
.marqueeCard.card-gradient-3 {
  background: linear-gradient(135deg, #007cf0, #00dfd8);
  color: #fff;
}
.marqueeCard.card-gradient-4 {
  background: linear-gradient(135deg, #4353ff, #90caf9);
  color: #fff;
}
.catButtonWrapper {
  position: relative;
  display: inline-block;
  z-index: 1;
  margin-top: 40px;
}
.catBtn {
  position: relative;
  background: #000;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
  padding: 16px 48px;
  border-radius: 100px;
  border: none;
  cursor: pointer;
  z-index: 2;
  transition: transform 0.2s ease;
}
.catBtn:hover {
  transform: scale(1.05);
}
.catPaw {
  position: absolute;
  top: -35px;
  right: 20px;
  width: 40px;
  height: 40px;
  z-index: 1;
  pointer-events: none;
}
.catPaw svg {
  width: 100%;
  height: 100%;
  fill: #333;
}
:root {
  --welcome-bg-gradient: radial-gradient(circle at 10% 20%, #e9e3ff, #b5fffc);
  --welcome-text-primary: #1f1f1f;
  --welcome-text-secondary: #666;
  --welcome-text-tertiary: rgba(0, 0, 0, 0.65);
  --welcome-card-bg: rgba(255, 255, 255, 0.3);
  --welcome-card-border: rgba(255, 255, 255, 0.4);
  --welcome-card-shadow: rgba(0, 0, 0, 0.05);
  --welcome-glass-bg: rgba(255, 255, 255, 0.25);
  --welcome-glass-border: rgba(255, 255, 255, 0.3);
  --welcome-blob-1: radial-gradient(circle at center, #ff718b, #ffeced);
  --welcome-blob-2: radial-gradient(circle at center, #87e6fb, #6f9eff);
  --welcome-blob-3: radial-gradient(circle at center, #b5fffc, #ffdee9);
  --welcome-btn-bg: #000;
  --welcome-btn-text: #fff;
  --welcome-footer-bg: #000;
  --welcome-footer-text: #fff;
  --welcome-feature-bg: rgba(255, 255, 255, 0.3);
  --welcome-feature-text: #444;
  --welcome-testimonial-bg: rgba(255, 255, 255, 0.4);
  --welcome-testimonial-border: #000;
  --welcome-testimonial-quote: #333;
}
:global(body.dark-mode) {
  --welcome-bg-gradient: radial-gradient(circle at 10% 20%, #1a1a2e, #16213e);
  --welcome-text-primary: #e0e0e0;
  --welcome-text-secondary: #a0a0a0;
  --welcome-text-tertiary: rgba(255, 255, 255, 0.65);
  --welcome-card-bg: rgba(0, 0, 0, 0.3);
  --welcome-card-border: rgba(255, 255, 255, 0.1);
  --welcome-card-shadow: rgba(0, 0, 0, 0.2);
  --welcome-glass-bg: rgba(0, 0, 0, 0.25);
  --welcome-glass-border: rgba(255, 255, 255, 0.1);
  --welcome-blob-1: radial-gradient(circle at center, #4a1a24, #2a1a1a);
  --welcome-blob-2: radial-gradient(circle at center, #1a3a4a, #1a2a3a);
  --welcome-blob-3: radial-gradient(circle at center, #1a4a4a, #2a1a2a);
  --welcome-btn-bg: #fff;
  --welcome-btn-text: #000;
  --welcome-footer-bg: #111;
  --welcome-footer-text: #eee;
  --welcome-feature-bg: rgba(0, 0, 0, 0.3);
  --welcome-feature-text: #ccc;
  --welcome-testimonial-bg: rgba(0, 0, 0, 0.4);
  --welcome-testimonial-border: #fff;
  --welcome-testimonial-quote: #ccc;
}
@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
@keyframes blobMove {
  0% {
    transform: translate(0, 0) scale(1);
  }
  33% {
    transform: translate(30px, -50px) scale(1.1);
  }
  66% {
    transform: translate(-20px, 20px) scale(0.9);
  }
  100% {
    transform: translate(0, 0) scale(1);
  }
}
@keyframes float {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
  100% {
    transform: translateY(0px);
  }
}
:global .welcome-container {
  min-height: 100vh;
  color: var(--welcome-text-primary);
  background: var(--welcome-bg-gradient);
  background-size: 200% 200%;
  animation: gradientShift 15s ease infinite;
  overflow-x: hidden;
}
:global .blob {
  position: absolute;
  height: 400px;
  width: 400px;
  border-radius: 50%;
  opacity: 0.6;
  filter: blur(100px);
  mix-blend-mode: screen;
  animation: blobMove 10s infinite ease-in-out;
}
:global .blob1 {
  top: 10%;
  left: 20%;
  background: var(--welcome-blob-1);
  animation-delay: 0s;
}
:global .blob2 {
  top: 40%;
  right: 15%;
  background: var(--welcome-blob-2);
  animation-delay: 5s;
}
:global .blob3 {
  bottom: 10%;
  left: 10%;
  background: var(--welcome-blob-3);
  animation-delay: 10s;
}
:global .hero-visual {
  max-width: 1400px;
  margin: 60px auto 0;
  perspective: 2000px;
}
:global .hero-visual .visual-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 32px;
  padding: 20px;
}
:global .hero-visual .visual-grid .visual-item {
  position: relative;
  cursor: pointer;
  transform-style: preserve-3d;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  min-height: 320px;
}
:global .hero-visual .visual-grid .visual-item .visual-item-inner {
  background: var(--welcome-card-bg);
  backdrop-filter: blur(10px);
  border: 1px solid var(--welcome-card-border);
  border-radius: 24px;
  padding: 48px 32px;
  text-align: center;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 32px var(--welcome-card-shadow);
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}
:global .hero-visual .visual-grid .visual-item .visual-item-inner::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0) 100%);
  transform: translateX(-100%);
  transition: transform 0.6s ease;
}
:global .hero-visual .visual-grid .visual-item:hover .visual-item-inner {
  border-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 24px 64px rgba(0, 0, 0, 0.1);
  background: rgba(255, 255, 255, 0.5);
}
:global .hero-visual .visual-grid .visual-item:hover .visual-item-inner::before {
  transform: translateX(100%);
}
:global .hero-visual .visual-grid .visual-item .visual-icon {
  font-size: 5rem;
  margin-bottom: 24px;
  filter: drop-shadow(0 4px 12px rgba(0, 0, 0, 0.1));
  transition: filter 0.3s ease;
  position: relative;
}
:global .hero-visual .visual-grid .visual-item:hover .visual-icon {
  filter: drop-shadow(0 8px 20px rgba(0, 0, 0, 0.2));
}
:global .hero-visual .visual-grid .visual-item .visual-name {
  font-size: 1.4rem;
  color: var(--welcome-text-primary);
  font-weight: 700;
  line-height: 1.3;
  margin-bottom: 12px;
  position: relative;
}
:global .hero-visual .visual-grid .visual-item .visual-desc {
  font-size: 0.95rem;
  color: var(--welcome-text-secondary);
  line-height: 1.5;
  max-width: 280px;
  position: relative;
}
:global .feature-section {
  max-width: 1280px;
  margin: 0 auto;
  padding: 120px 6vw;
  position: relative;
}
:global .feature-section.feature-hero {
  background: var(--welcome-glass-bg);
  backdrop-filter: blur(10px);
  border-radius: 30px;
  margin-bottom: 40px;
  border: 1px solid var(--welcome-glass-border);
  box-shadow: 0 20px 40px var(--welcome-card-shadow);
}
:global .feature-section.feature-hero::before {
  content: none;
}
:global .feature-section.reverse .feature-content {
  flex-direction: row-reverse;
}
:global .feature-section .feature-content {
  display: flex;
  align-items: center;
  gap: 80px;
}
@media (max-width: 968px) {
  :global .feature-section .feature-content {
    flex-direction: column !important;
    gap: 48px;
  }
}
:global .feature-section .feature-text-block {
  flex: 1;
  max-width: 560px;
}
:global .feature-section .feature-text-block .feature-eyebrow {
  display: inline-block;
  font-size: 0.9rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: var(--welcome-text-secondary);
  margin-bottom: 16px;
}
:global .feature-section .feature-text-block .feature-title {
  font-size: clamp(2rem, 4vw, 3rem);
  font-weight: 800;
  line-height: 1.2;
  letter-spacing: -1px;
  margin-bottom: 24px;
  color: var(--welcome-text-primary);
}
:global .feature-section .feature-text-block .feature-description {
  font-size: 1.2rem;
  color: var(--welcome-text-tertiary);
  line-height: 1.7;
  margin-bottom: 32px;
}
:global .feature-section .feature-text-block .feature-list {
  list-style: none;
  padding: 0;
  margin: 0 0 32px 0;
}
:global .feature-section .feature-text-block .feature-list li {
  position: relative;
  padding-left: 32px;
  margin-bottom: 16px;
  font-size: 1.05rem;
  color: var(--welcome-text-tertiary);
  line-height: 1.6;
}
:global .feature-section .feature-text-block .feature-list li::before {
  content: 'âœ“';
  position: absolute;
  left: 0;
  top: 0;
  font-weight: bold;
  color: var(--welcome-text-primary);
  font-size: 1.2rem;
}
:global .feature-section .feature-text-block .inline-testimonial {
  background: var(--welcome-testimonial-bg);
  border-left: 4px solid var(--welcome-testimonial-border);
  padding: 24px;
  margin: 32px 0;
  border-radius: 8px;
}
:global .feature-section .feature-text-block .inline-testimonial blockquote {
  font-size: 1.05rem;
  font-style: italic;
  color: var(--welcome-testimonial-quote);
  margin: 0 0 16px 0;
  line-height: 1.6;
}
:global .feature-section .feature-text-block .inline-testimonial .testimonial-author {
  display: flex;
  flex-direction: column;
  gap: 4px;
}
:global .feature-section .feature-text-block .inline-testimonial .testimonial-author strong {
  font-weight: 600;
  color: var(--welcome-text-primary);
}
:global .feature-section .feature-text-block .inline-testimonial .testimonial-author span {
  font-size: 0.9rem;
  color: var(--welcome-text-secondary);
}
:global .feature-section .feature-visual {
  flex: 1;
  max-width: 600px;
}
:global .feature-section .feature-visual .visual-grid-section .tool-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  gap: 24px;
}
:global .feature-section .feature-visual .visual-grid-section .tool-grid-stacked {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  perspective: 1000px;
}
:global .feature-section .feature-visual .visual-grid-section .tool-card {
  background: var(--welcome-card-bg);
  border: 1px solid var(--welcome-card-border);
  border-radius: 18px;
  padding: 32px 24px;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 6px 20px var(--welcome-card-shadow);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  backdrop-filter: blur(10px);
}
:global .feature-section .feature-visual .visual-grid-section .tool-card:hover {
  border-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 16px 48px rgba(0, 0, 0, 0.18);
  transform: translateY(-12px) scale(1.02);
  background: rgba(255, 255, 255, 0.6);
}
:global .feature-section .feature-visual .visual-grid-section .tool-card .tool-icon {
  font-size: 3rem;
  margin-bottom: 16px;
  filter: grayscale(0.2);
  transition: filter 0.3s ease;
}
:global .feature-section .feature-visual .visual-grid-section .tool-card:hover .tool-icon {
  filter: grayscale(0);
}
:global .feature-section .feature-visual .visual-grid-section .tool-card .tool-name {
  font-size: 1.05rem;
  font-weight: 600;
  margin: 0 0 10px 0;
  color: var(--welcome-text-primary);
}
:global .feature-section .feature-visual .visual-grid-section .tool-card .tool-desc {
  font-size: 0.9rem;
  color: var(--welcome-text-secondary);
  margin: 0;
  line-height: 1.5;
}
:global .feature-section .feature-visual .visual-showcase {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 0;
  perspective: 1200px;
}
:global .feature-section .feature-visual .visual-showcase.stacked .showcase-card {
  position: relative;
  margin-bottom: -140px;
}
:global .feature-section .feature-visual .visual-showcase.stacked .showcase-card:last-child {
  margin-bottom: 0;
}
:global .feature-section .feature-visual .visual-showcase.stacked .showcase-card:nth-child(odd) {
  transform: rotate(-2deg);
  margin-left: -20px;
}
:global .feature-section .feature-visual .visual-showcase.stacked .showcase-card:nth-child(even) {
  transform: rotate(2deg);
  margin-right: -20px;
}
:global .feature-section .feature-visual .visual-showcase.stacked .showcase-card:hover {
  transform: rotate(0deg) !important;
  z-index: 100 !important;
}
:global .feature-section .feature-visual .visual-showcase .showcase-card {
  background: var(--welcome-card-bg);
  border: 1px solid var(--welcome-card-border);
  border-radius: 20px;
  padding: 40px 32px;
  text-align: center;
  cursor: pointer;
  box-shadow: 0 8px 24px var(--welcome-card-shadow);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: blur(10px);
}
